.mobile-list-container {
  display: flex;
  flex-wrap: wrap;
}

.mobile-card {
  width: 380px;
  margin: 16px;
  height: 96%;
  transition: transform 0.3s;
}

.mobile-card:hover {
  transform: scale(1.05);
}

.mobile-info {
  padding: 16px;
  display: flex;
  flex-direction: column;
}

.brand {
  font-size: 18px;
  font-weight: bold;
  color: #333;
}

.model {
  font-size: 16px;
  color: #555;
  margin-bottom: 8px;
}

.price {
  font-size: 18px;
  color: #27ae60;
  font-weight: bold;
}

.ant-rate {
  margin-top: 8px;
}
